.sticky-header{display:flex;align-items:center;height:48px;width:100vw;top:56px;box-shadow:inset 0 -1px 0 0 rgba(29,29,29,.07);padding-left:16px;background-color:#fff;overflow-y:hidden;white-space:nowrap}.sticky-header__categories__item{padding-left:24px;cursor:pointer;display:inline-block;margin-right:24px;font-family:Gotham Pro;position:relative;height:100%;padding-top:14px;padding-bottom:10px;font-size:16px;font-weight:500;line-height:1.5;letter-spacing:normal;color:#1d1d1d}.sticky-header__categories__item a{color:#1d1d1d;text-decoration:none}.sticky-header__categories__item-monopizza{padding-left:36px;position:relative}.sticky-header__categories__item-monopizza:before{position:absolute;content:"";background-image:url(/img/icons/ic-monopizza.png);width:25px;height:25px;bottom:-5px;left:0}.sticky-header__categories__item.selected{position:relative}.sticky-header__categories__item.selected a{color:#e30613}.sticky-header__categories__item__sub-menu{display:none}.sticky-header__categories__placeholder{cursor:default;position:relative;background:hsla(0,0%,85.1%,.8);overflow:hidden;border-radius:26px;height:18px;margin-bottom:5px}.sticky-header__categories__placeholder:after{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background:linear-gradient(90deg,hsla(0,0%,85.1%,.8),hsla(0,0%,92.2%,.8),hsla(0,0%,85.1%,.8));-webkit-animation:progress 1s ease-in-out infinite;animation:progress 1s ease-in-out infinite}@-webkit-keyframes progress{0%{transform:translate3d(-100%,0,0)}to{transform:translate3d(100%,0,0)}}@keyframes progress{0%{transform:translate3d(-100%,0,0)}to{transform:translate3d(100%,0,0)}}