.modal-backdrop{position:fixed;top:0;left:0;z-index:2;width:100vw;height:100vh;background-color:#000;opacity:.5}.city_modal{height:100%;width:100%;-webkit-backdrop-filter:blur(3px);backdrop-filter:blur(3px);position:absolute;top:0;left:0;z-index:3}.city_modal-center_container{max-height:600px;position:absolute;z-index:4;left:50%;margin:15% -50% 15% auto;transform:translate(-50%,-50%);display:flex;justify-content:center}.m_m-box{margin-top:10%;border-radius:8px;width:1150px;max-height:600px;overflow:hidden;background-color:#fff}.m_m-header{display:flex}.m_m-header>h1{font-family:Pusia;margin:26px auto}.m_m-body{max-height:400px;overflow-y:scroll;display:flex;flex-direction:column}.m_m-body::-webkit-scrollbar-track{-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,.3);background-color:#f5f5f5;border-radius:10px}.m_m-body::-webkit-scrollbar{width:18px;background-color:#f5f5f5}.m_m-body::-webkit-scrollbar-thumb{border-radius:10px;-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,.3);background-color:#e30613}.m_m-body button{background:transparent;font-size:1.4rem;cursor:pointer;padding-top:20px;padding-bottom:8px;font-weight:500;color:rgba(0,0,0,.75);border-bottom:1px solid rgba(29,29,29,.07)}.m_m-footer button{box-sizing:border-box;width:95%;margin-bottom:18px;margin-left:18px;margin-right:18px;border-radius:28px;height:auto;padding-top:16px;padding-bottom:12px;background-color:#e30613;color:#fff;border:none;text-align:center;cursor:pointer;font-family:Gotham Pro;font-size:1rem;font-weight:500;font-style:normal;font-stretch:normal;line-height:1.71;text-transform:capitalize}